// @flow
import {
  Flex,
  Heading,
  HStack,
  Image,
  Link,
  PinInput,
  PinInputField,
  Stack,
  Text,
  VStack,
} from "@chakra-ui/react";
import Images_Icons from "../../../constants/icons-images";
import MeProtocolButton from "../../../components/MeProtocolFormikButton";
import TopIconBar from "../../../components/TopIconBar";
import { useEffect, useState } from "react";
import { useStyles } from "../Login/Login";
import { useLocation, useNavigate } from "react-router-dom";
import axios from "axios";
import { BASE_URL } from "../../../config";
import { useDispatch } from "react-redux";
import { Dispatch } from "redux";
import { saveTokenAndUser } from "../../../redux/AuthSlice";
import { nanoid } from "@reduxjs/toolkit";
import { getUserCollection } from "../../../redux/UserSlice";
import { GLOBAL_PREFIX } from "../../../constants";

export const CodeInputPage = () => {
  let navigate = useNavigate();
  const dispatch = useDispatch<Dispatch>();
  // const {user_data} = useSelector((state: RootState) => state.userReducer)

  const { state }: any = useLocation();

  const { root, boxLeft, boxLogin, leftImg, loginColumn, head, desc } =
    useStyles();

  const [buttonDisabled, setButtonDisabled] = useState<boolean>(true);
  const [code, setCode] = useState<string>("");
  const [loading, setLoading] = useState<boolean>(false);
  const [errMsg, setErrMsg] = useState<string>("");

  useEffect(() => {
    // console.log(state?.state?.user?.userId, "KKKK");
  }, []);

  async function VerifyPhoneCode() {
    if (code.length < 5) return;
    setLoading(true);
    try {
      const { data }: any = await axios.post(`${BASE_URL}/users/phone/verify`, {
        userId: state?.state?.user?.userId,
        phone: state?.value,
        phoneCode: code,
      });

      if (data?.isVerified?.token) {
        if (data) {
          dispatch(saveTokenAndUser(data?.isVerified));
          //  dispatch(getUserInfo({ token: payload?.access_token?.token }));
          dispatch(getUserCollection({ token: data?.isVerified?.token }));

          let config = {
            method: "get",
            url: `${BASE_URL}/pointregistry`,
            headers: {
              "x-access-token": `${data?.isVerified?.token}`,
            },
          };
          const { data: resData }: any = await axios(config);

          if (resData?.userPoints?.length < 1) {
            navigate(`${GLOBAL_PREFIX}/syncRewards`);
          } else {
            navigate(`${GLOBAL_PREFIX}/dashboard`);
          }
          // navigate(`${GLOBAL_PREFIX}/syncRewards`);
          // navigate(`${GLOBAL_PREFIX}/dashboard`);
        }
      }

      // console.log(data, "YYYUIPOOO")
    } catch (e: any | unknown) {
      // console.log(e?.response?.data?.error, "YYYUIPOOO")
      setErrMsg(e?.response?.data?.error);
    } finally {
      setLoading(false);
      setCode("");
    }
  }

  return (
    <>
      <TopIconBar />
      <Flex {...root}>
        <Flex {...boxLeft}>
          <Image src={Images_Icons.AuthImage} alt="Me Protocol" {...leftImg} />
        </Flex>
        <Flex {...boxLogin}>
          <Stack
            {...loginColumn}
            w={{
              base: "70vw",
              md: "40vw",
            }}
          >
            <Heading {...head} pb={2}>
              We sent you a verification code
            </Heading>
            <Text {...desc}>You have to give it back, though.</Text>

            <HStack w={"100%"} h={"60px"}>
              <PinInput
                onComplete={(e) => {
                  setCode(e);
                  setErrMsg("");
                }}
                otp
                placeholder={"*"}
              >
                {[1, 2, 3, 4, 5, 6]?.map(() => (
                  <PinInputField
                    key={nanoid()}
                    bg={"grey"}
                    borderRadius={10}
                    borderWidth={0}
                    w={"14%"}
                    h={"100%"}
                  />
                ))}
              </PinInput>
            </HStack>
            {errMsg && (
              <Text {...desc} color={"red"}>
                {errMsg}
              </Text>
            )}
            <Flex w={"35vMax"}>
              <MeProtocolButton
                isLoading={loading}
                onClick={VerifyPhoneCode}
                isDisabled={code.length < 5}
                name={"That's the code"}
              />
            </Flex>

            <Text {...desc} pt={"10px"}>
              Didn’t receive the code?{" "}
              <Link href="#" textDecoration={"underline"}>
                {" "}
                Send it again.
              </Link>
            </Text>
          </Stack>
        </Flex>
      </Flex>
    </>
  );
};

export default CodeInputPage;
